import React from 'react';
import ReactDOM from 'react-dom';
import '../index.css';
import {Nav,NavDropdown,MenuItem,Navbar,FormGroup,FormControl,Button,NavItem,Glyphicon,Table,Well} from 'react-bootstrap';
import {NavAutoDropdown} from '../Components/navautodrop';
import {Category} from '../Components/category';
import {LinkContainer} from 'react-router-bootstrap';
import {Link} from 'react-router-dom';
const TopBar = ()=>(
  <Navbar collapseOnSelect>
    <Navbar.Header>
      <Navbar.Toggle/>
    </Navbar.Header>
    <Navbar.Collapse>
    <Nav pullRight>
      <NavDropdown title={"Language"} eventKey="1" id="lang-dropdown">
        <MenuItem eventKey="1.1" active>English</MenuItem>
        <MenuItem eventKey="1.2">French</MenuItem>
      </NavDropdown>
      <NavDropdown title={"Currency"} eventKey="2" id="curr-dropdown">
        <MenuItem eventKey="2.1" active>US Dollar ($)</MenuItem>
        <MenuItem eventKey="2.2">Euro (€)</MenuItem>
      </NavDropdown>
      <LinkContainer to='/sample/eshop/cart'><NavItem eventKey={3} id="top-cart">Cart</NavItem></LinkContainer>
      <NavItem eventKey={4} href="#" id="top-account">Account</NavItem>
    </Nav>
  </Navbar.Collapse>
  </Navbar>
);
const MidBar = ()=>(
  <Navbar>
    <Navbar.Header>
      <Navbar.Brand><Link to='/sample/eshop/'>E-Shop</Link></Navbar.Brand>
      <Navbar.Form pullRight id="navbar-form">
        <FormGroup >
          <FormControl type="text" placeholder="Search" id="search-input"/>
        </FormGroup>
        {'  '}
        <Button type="submit" id="submit-btn">Search</Button>
      </Navbar.Form>
    </Navbar.Header>
    <Nav pullRight id="mid-nav">
      <LinkContainer to='/sample/eshop/cart'><NavItem eventKey={1} href="#">Cart</NavItem></LinkContainer>
      <NavItem eventKey={2} href="#">Account</NavItem>
    </Nav>
  </Navbar>
);
let menCat = [
  {
    title : "Clothing",
    items : [["Casual Shirts","Jeans","Formal Shirts","Blazer"],["Tops","T-Shirts","Shorts","Pants"],["Costumes"],["Costumes"],["Tops","T-Shirts","Shorts","Pants"]]
  },
  {
    title : "Footwear",
    items : [["Boots","Brogues","Boat Shoes","Casual Shoes"],["Holiday Shoes","Loafers","Sandals","Slippers","Smart Shoes"]]
  }
];

const CatBar = ()=>(

  <Navbar>
    <Nav className="center-nav">

      <Category title="Men" categories={menCat}/>
      <Category title="Women" categories={menCat}/>
    </Nav>
  </Navbar>


);

export class NavbarTop extends React.Component{
  render(){
    return(
      <div>
        <TopBar/>
        <MidBar/>
        <CatBar/>
      </div>
    );

  }
}



