import React from 'react';
import ReactDOM from 'react-dom';
import Slider from 'react-slick';
import {Link} from 'react-router-dom';
import img1 from '../images/denim1.jpg';

function Arrow(props){
  const {className, style, onClick} = props;
  return(
    <div
      className={className}
      style={{...style, display: 'block', color: 'black'}}
      onClick={onClick}
      ></div>
  );
}

export default class HomeSlide extends React.Component{

  render(){
    var settings = {
       dots: true,
       infinite: true,
       speed: 500,
       slidesToShow: 3,
       slidesToScroll: 3,
       nextArrow: <Arrow />,
       prevArrow: <Arrow />,
       responsive : [ { breakpoint: 768, settings: { slidesToShow: 1 } }, { breakpoint: 1024, settings: { slidesToShow: 2 } } ]
     };
    return(
      <div>
        <h2>{this.props.title}</h2>
        <Slider {...settings}>
          {this.props.slides.map((item,i)=>{
            return(
              <div key={i}>
                <Link to={item.link}>
                  <div style={{padding:'5px'}}>
                    <img src={img1}></img>
                    <div style={{textAlign:'center'}}>{item.name}</div>
                    <span>{item.price+" $"}</span>
                  </div>
                </Link>


              </div>
            )
          })}
        </Slider>
      </div>
    );
  }
}



