import React from 'react';
import ReactDOM from 'react-dom';
import {NavAutoDropdown} from './navautodrop';
import {NavItem} from 'react-bootstrap';
export class Category extends React.Component{

  constructor(props) {
    super(props);

  }
  render(){
    return(
      <NavAutoDropdown title={this.props.title} id={this.props.title+"-cat"}>
        {this.props.categories.map((item,i)=>{
          return(this.createCategory(item,i))
        })}
      </NavAutoDropdown>
    );
  }
  
  createCategory(category,i){
    return(
      <dl key={i} className="section">
        <dt>{category.title}</dt>
        <dd>
           {category.items.map((item,i)=>{
	            return(this.createItems(item,i))
            })}
        </dd>
      </dl>
    );
  }

  createItems(list,i){
    return(

      <ul key={i} className="items">
        {list.map(function(item,i){
          return (<li key={i}>{item}</li>);
        })}
      </ul>

    );
  }
}



